import react, {Component} from 'react';
import styled from 'styled-components';
import _find from 'lodash/find';

import TitleBar from '../components/TitleBar';
import Slide from '../components/Slide';

const Wrapper = styled.div`
    display: flex;
    flex-direction: column;
    position: absolute;
    width: 100%;
    height: 100%;
`;

const Content = styled.div`
    flex-grow: 1;
    position: relative;
`;

class PhotoPage extends Component {
    state = {
        baseUrl: '',
        photo: undefined,
    };

    update = data => {
        const {url} = this.props;
        const album = _find(data.albums, {id: url.query.albumId});

        this.setState(() => ({
            baseUrl: data.baseUrl,
            photo: _find(album.photos, {id: url.query.photoId}),
        }));
    };

    /* getInitialProps = async ({req}) => {
        const res = await fetch('static/data.json');
        const json = await res.json();
        this.update(data);
    }; */

    componentDidMount() {
        fetch('/static/data.json')
            .then(res => {
                return res.json();
            })
            .then(json => {
                this.update(json);
            });
    }

    render() {
        const {photo = {title: '', url: ''}, baseUrl} = this.state;
        const {url} = this.props;

        return (
            <Wrapper>
                <TitleBar title={'React Photos'} />
                <TitleBar title={photo.title} backLink={{pathname: '/album', query: {id: url.query.albumId}}} />
                <Content>
                    {photo ? <Slide imageUrl={photo.url} baseUrl={baseUrl} title={photo.title} /> : <Loading />}
                </Content>
            </Wrapper>
        );
    }
}

export default PhotoPage;



